"use client";
import { motion } from 'framer-motion';
import { FiEdit, FiTrash2, FiEye } from 'react-icons/fi';
import { Car } from '../types';

const CarTable = ({ cars, onEdit, onDelete }: {
  cars: Car[];
  onEdit: (car: Car) => void;
  onDelete: (id: string) => void;
}) => {
  const containerVariants = {
    hidden: { opacity: 0 },
    show: {
      opacity: 1,
      transition: {
        staggerChildren: 0.05,
      },
    },
  };

  const rowVariants = {
    hidden: { opacity: 0, y: 20 },
    show: { opacity: 1, y: 0 },
  };

  return (
    <motion.div
      className="overflow-x-auto"
      initial="hidden"
      animate="show"
      variants={containerVariants}
    >
      <table className="min-w-full divide-y divide-gray-200">
        <thead className="bg-gray-50">
          <tr>
            <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Plate</th>
            <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Owner</th>
            <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Model</th>
            <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Type</th>
            <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Year</th>
            <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Engine</th>
            <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Efficiency</th>
            <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Mileage</th>
            <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Fuel</th>
            <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Status</th>
            <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Location</th>
            <th className="px-4 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Registered</th>
            <th className="px-4 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">Actions</th>
          </tr>
        </thead>
        <tbody className="bg-white divide-y divide-gray-200">
          {cars.map((car) => (
            <motion.tr 
              key={car.id} 
              variants={rowVariants}
              whileHover={{ scale: 1.01, boxShadow: '0 4px 6px -1px rgba(0, 0, 0, 0.1)' }}
              className="hover:bg-gray-50"
            >
              {/* Identification */}
              <td className="px-4 py-4 whitespace-nowrap font-medium text-gray-900">
                {car.plateNumber}
              </td>
              
              {/* Owner Info */}
              <td className="px-4 py-4 whitespace-nowrap">
                <div className="text-gray-900">{car.ownerName}</div>
                <div className="text-gray-500 text-xs">{car.ownerPhone}</div>
              </td>
              
              {/* Vehicle Info */}
              <td className="px-4 py-4 whitespace-nowrap text-gray-900">
                {car.model}
              </td>
              <td className="px-4 py-4 whitespace-nowrap">
                <span className={`px-2 py-1 text-xs rounded-full ${
                  car.carType === 'Bus' ? 'bg-purple-100 text-purple-800' :
                  car.carType === 'Minibus' ? 'bg-blue-100 text-blue-800' :
                  'bg-gray-100 text-gray-800'
                }`}>
                  {car.carType}
                </span>
              </td>
              <td className="px-4 py-4 whitespace-nowrap text-gray-900">
                {car.manufactureYear}
              </td>
              
              {/* Technical Specs */}
              <td className="px-4 py-4 whitespace-nowrap text-gray-900">
                {car.motorCapacity} cc
              </td>
              <td className="px-4 py-4 whitespace-nowrap text-gray-900">
                {car.kmPerLiter} km/l
              </td>
              <td className="px-4 py-4 whitespace-nowrap text-gray-900">
                {car.totalKm} km
              </td>
              <td className="px-4 py-4 whitespace-nowrap">
                <span className={`px-2 py-1 text-xs rounded-full ${
                  car.fuelType === 'Diesel' ? 'bg-yellow-100 text-yellow-800' :
                  car.fuelType === 'Electric' ? 'bg-green-100 text-green-800' :
                  'bg-gray-100 text-gray-800'
                }`}>
                  {car.fuelType}
                </span>
              </td>
              
              {/* Status */}
              <td className="px-4 py-4 whitespace-nowrap">
                <span className={`px-2 py-1 text-xs rounded-full ${
                  car.status === 'Active' ? 'bg-green-100 text-green-800' :
                  car.status === 'Maintenance' ? 'bg-yellow-100 text-yellow-800' :
                  'bg-red-100 text-red-800'
                }`}>
                  {car.status}
                </span>
              </td>
              
              {/* Location */}
              <td className="px-4 py-4 whitespace-nowrap text-gray-900">
                {car.parkingLocation}
              </td>
              
              {/* Registration */}
              <td className="px-4 py-4 whitespace-nowrap text-gray-900 text-sm">
                {new Date(car.registeredDate).toLocaleDateString()}
              </td>
              
              {/* Actions */}
              <td className="px-4 py-4 whitespace-nowrap text-right text-sm font-medium">
                <div className="flex justify-end space-x-1">
                  <a 
                    href={`/tms-modules/admin/car-management/view-car/${car.id}`}
                    className="text-blue-600 hover:text-blue-900 p-1 rounded-full hover:bg-blue-50"
                    title="View details"
                  >
                    <FiEye className="h-5 w-5" />
                  </a>
                  <button
                    onClick={() => onEdit(car)}
                    className="text-green-600 hover:text-green-900 p-1 rounded-full hover:bg-green-50"
                    title="Edit"
                  >
                    <FiEdit className="h-5 w-5" />
                  </button>
                  <button
                    onClick={() => onDelete(car.id)}
                    className="text-red-600 hover:text-red-900 p-1 rounded-full hover:bg-red-50"
                    title="Delete"
                  >
                    <FiTrash2 className="h-5 w-5" />
                  </button>
                </div>
              </td>
            </motion.tr>
          ))}
        </tbody>
      </table>
    </motion.div>
  );
};

export default CarTable;