"use client";
import { motion } from 'framer-motion';
import { FiActivity, FiCheckCircle, FiAlertTriangle, FiClock, FiTrendingUp, FiTrendingDown } from 'react-icons/fi';
import { Car } from '../types';

interface StatsProps {
  cars: Car[];
}

interface StatItem {
  title: string;
  value: string | number;
  icon: React.ReactNode;
  change: string;
  changeType: 'positive' | 'negative' | 'neutral';
  trendIcon?: React.ReactNode;
}

const Stats = ({ cars }: StatsProps) => {
  // Calculate statistics
  const totalCars = cars.length;
  const activeCars = cars.filter(car => car.status === 'Active').length;
  const maintenanceCars = cars.filter(car => car.status === 'Maintenance').length;
  const averageKmPerLiter = cars.length > 0 
    ? (cars.reduce((sum, car) => sum + parseFloat(car.kmPerLiter), 0) / cars.length).toFixed(1)
    : '0';

  // Mock data for changes (in a real app, you'd compare with previous period)
  const stats: StatItem[] = [
    {
      title: 'Pending Cars',
      value: totalCars,
      icon: <FiActivity className="h-6 w-6 text-blue-500" />,
      change: '+2',
      changeType: 'positive',
      trendIcon: <FiTrendingUp className="ml-1 text-green-500" />
    },
    {
      title: 'Approved Cars',
      value: activeCars,
      icon: <FiCheckCircle className="h-6 w-6 text-green-500" />,
      change: '+1',
      changeType: 'positive',
      trendIcon: <FiTrendingUp className="ml-1 text-green-500" />
    },
    {
      title: 'Passed but Warning Cars',
      value: averageKmPerLiter,
      icon: <FiClock className="h-6 w-6 text-purple-500" />,
      change: '-0.2',
      changeType: 'negative',
      trendIcon: <FiTrendingDown className="ml-1 text-red-500" />
    },
    {
        title: 'Cers In Maintenance',
        value: maintenanceCars,
        icon: <FiAlertTriangle className="h-6 w-6 text-yellow-500" />,
        change: '0',
        changeType: 'neutral'
      },
  ];

  // Animation variants
  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.1
      }
    }
  };

  const itemVariants = {
    hidden: { y: 20, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1,
      transition: {
        duration: 0.5
      }
    }
  };

  return (
    <motion.div
      className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6"
      variants={containerVariants}
      initial="hidden"
      animate="visible"
    >
      {stats.map((stat, index) => (
        <motion.div
          key={index}
          variants={itemVariants}
          className="bg-white rounded-xl shadow-lg p-6 hover:shadow-xl transition-shadow"
          whileHover={{ scale: 1.02 }}
        >
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-gray-500">{stat.title}</p>
              <p className="mt-1 text-3xl font-semibold text-gray-900">
                {stat.value}
              </p>
            </div>
            <div className="p-3 rounded-full bg-gray-50">
              {stat.icon}
            </div>
          </div>
          <div className="mt-4 flex items-center">
            <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${
              stat.changeType === 'positive' 
                ? 'bg-green-100 text-green-800' 
                : stat.changeType === 'negative' 
                  ? 'bg-red-100 text-red-800' 
                  : 'bg-gray-100 text-gray-800'
            }`}>
              {stat.change}
              {stat.trendIcon}
            </span>
            <span className="ml-2 text-xs text-gray-500">vs last month</span>
          </div>
        </motion.div>
      ))}
    </motion.div>
  );
};

export default Stats;