"use client";
import { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { FiPlus, FiSearch, FiFilter, FiRefreshCw } from 'react-icons/fi';
import CarTable from './components/CarTable';
import CarForm from './components/CarForm';
import Stats from './components/Stats';
import { fetchCars, createCar, updateCar, deleteCar, searchCars } from '../api/carServices';
import { Car } from './types';
import Swal from 'sweetalert2';

export default function ManageCars() {
  const [cars, setCars] = useState<Car[]>([]);
  const [filteredCars, setFilteredCars] = useState<Car[]>([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [isFormOpen, setIsFormOpen] = useState(false);
  const [selectedCar, setSelectedCar] = useState<Car | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  // Load cars on component mount
  useEffect(() => {
    loadCars();
  }, []);

  const loadCars = async () => {
    try {
      setIsLoading(true);
      const { success, data, message } = await fetchCars();
      if (success) {
        setCars(data);
        setFilteredCars(data);
      } else {
        throw new Error(message);
      }
    } catch (error) {
      Swal.fire({
        title: 'Error!',
        text: error instanceof Error ? error.message : 'Failed to load cars',
        icon: 'error',
        showClass: { popup: 'animate__animated animate__shakeX' }
      });
    } finally {
      setIsLoading(false);
    }
  };

  // Handle search functionality
  useEffect(() => {
    const timer = setTimeout(() => {
      if (searchTerm) {
        handleSearch(searchTerm);
      } else {
        setFilteredCars(cars);
      }
    }, 300);

    return () => clearTimeout(timer);
  }, [searchTerm, cars]);

  const handleSearch = async (query: string) => {
    try {
      setIsLoading(true);
      const { success, data, message } = await searchCars(query);
      if (success) {
        setFilteredCars(data);
      } else {
        throw new Error(message);
      }
    } catch (error) {
      Swal.fire({
        title: 'Error!',
        text: error instanceof Error ? error.message : 'Search failed',
        icon: 'error',
        showClass: { popup: 'animate__animated animate__shakeX' }
      });
    } finally {
      setIsLoading(false);
    }
  };

  const handleAddCar = async (newCar: Car) => {
    try {
      const { success, data, message } = await createCar(newCar);
      if (success) {
        setCars(prev => [...prev, data]);
        setFilteredCars(prev => [...prev, data]);
        setIsFormOpen(false);
        Swal.fire({
          title: 'Success!',
          text: 'Car registered successfully',
          icon: 'success',
          showClass: { popup: 'animate__animated animate__bounceIn' }
        });
      } else {
        throw new Error(message);
      }
    } catch (error) {
      Swal.fire({
        title: 'Error!',
        text: error instanceof Error ? error.message : 'Failed to register car',
        icon: 'error',
        showClass: { popup: 'animate__animated animate__shakeX' }
      });
    }
  };

  const handleUpdateCar = async (updatedCar: Car) => {
    try {
      const { success, data, message } = await updateCar(updatedCar.id, updatedCar);
      if (success) {
        setCars(prev => prev.map(car => car.id === data.id ? data : car));
        setFilteredCars(prev => prev.map(car => car.id === data.id ? data : car));
        setSelectedCar(null);
        Swal.fire({
          title: 'Success!',
          text: 'Car updated successfully',
          icon: 'success',
          showClass: { popup: 'animate__animated animate__bounceIn' }
        });
      } else {
        throw new Error(message);
      }
    } catch (error) {
      Swal.fire({
        title: 'Error!',
        text: error instanceof Error ? error.message : 'Failed to update car',
        icon: 'error',
        showClass: { popup: 'animate__animated animate__shakeX' }
      });
    }
  };

  const handleDeleteCar = async (id: number) => {
    const result = await Swal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes, delete it!',
      showClass: { popup: 'animate__animated animate__fadeInDown' },
      hideClass: { popup: 'animate__animated animate__fadeOutUp' }
    });

    if (result.isConfirmed) {
      try {
        const { success, message } = await deleteCar(id);
        if (success) {
          setCars(prev => prev.filter(car => car.id !== id));
          setFilteredCars(prev => prev.filter(car => car.id !== id));
          Swal.fire({
            title: 'Deleted!',
            text: 'Car has been deleted.',
            icon: 'success',
            showClass: { popup: 'animate__animated animate__bounceIn' }
          });
        } else {
          throw new Error(message);
        }
      } catch (error) {
        Swal.fire({
          title: 'Error!',
          text: error instanceof Error ? error.message : 'Failed to delete car',
          icon: 'error',
          showClass: { popup: 'animate__animated animate__shakeX' }
        });
      }
    }
  };

  return (
    <div className="p-6 space-y-6">
      {/* Header Section */}
      <motion.div
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
        className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4"
      >
        <h1 className="text-3xl font-bold text-gradient bg-clip-text text-transparent bg-gradient-to-r from-blue-500 to-purple-600">
          Car Fleet Management
        </h1>
        <div className="flex items-center gap-3 w-full md:w-auto">
          <div className="relative flex-1 md:w-64">
            <FiSearch className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" />
            <input
              type="text"
              placeholder="Search cars..."
              className="pl-10 pr-4 py-2 w-full rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
            />
          </div>
          <motion.button
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            className="p-2 rounded-lg bg-white border border-gray-300 hover:bg-gray-50 transition-colors"
          >
            <FiFilter className="text-gray-600" />
          </motion.button>
          <motion.button 
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            className="p-2 rounded-lg bg-white border border-gray-300 hover:bg-gray-50 transition-colors"
            onClick={loadCars}
          >
            <FiRefreshCw className="text-gray-600" />
          </motion.button>
        </div>
      </motion.div>

      {/* Stats Section */}
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 0.2, duration: 0.5 }}
      >
        <Stats cars={cars} />
      </motion.div>

      {/* Main Content */}
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 0.4, duration: 0.5 }}
        className="bg-white rounded-xl shadow-lg overflow-hidden"
      >
        {/* Table Header */}
        <div className="p-4 border-b border-gray-200 flex justify-between items-center">
          <h2 className="text-xl font-semibold text-gray-800">Car Fleet</h2>
          <motion.button
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            onClick={() => {
              setSelectedCar(null);
              setIsFormOpen(true);
            }}
            className="flex items-center gap-2 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
          >
            <FiPlus /> Register New Car
          </motion.button>
        </div>

        {/* Loading State */}
        {isLoading ? (
          <div className="p-8 flex justify-center items-center">
            <motion.div
              animate={{ rotate: 360 }}
              transition={{ duration: 1, repeat: Infinity, ease: "linear" }}
              className="h-12 w-12 border-t-2 border-b-2 border-blue-500 rounded-full"
            />
          </div>
        ) : (
          <CarTable 
            cars={filteredCars} 
            onEdit={(car) => {
              setSelectedCar(car);
              setIsFormOpen(true);
            }} 
            onDelete={handleDeleteCar} 
          />
        )}
      </motion.div>

      {/* Car Form Modal */}
      <AnimatePresence>
        {(isFormOpen || selectedCar) && (
          <CarForm
            car={selectedCar}
            onClose={() => {
              setIsFormOpen(false);
              setSelectedCar(null);
            }}
            onSubmit={selectedCar ? handleUpdateCar : handleAddCar}
          />
        )}
      </AnimatePresence>
    </div>
  );
}